* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: green;
}

.main-container {
  height: 70px;
  width: 500px;
  background-color: olive;
  text-align: center;
  margin-top: 100px;
  margin-left: 700px;
  padding: 10px;
}

.search-field, .submit-btn{
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
}

.search-field {
  font-size: 1rem;
  color: black;
}

.submit-btn {
  background-color: blue;
  color: white;
  font-weight: bolder;
  font-size: 1rem;
}

.main-card {
  background-color: white;
  height: 400px;
  width: 400px;
  box-shadow: 1px 2px 1px black;
  padding: 10px;
  display: inline-block;
}